import React from 'react';
import {  ToggleButton, ToggleButtonGroup } from '@mui/material';
import { darken, lighten } from '@mui/material/styles';


const textMap = {
  number: 'No.',
  partner:'パートナー',
  character:'キャラ',
  event:'イベント',
  case:'事件',
  actionPoint:'AP',
  logicPoint:'LP',
}

export const CommonToggleButtonGroup = ({ exclusive, value, onChange, options }) => {
  return (
    <ToggleButtonGroup
      exclusive={exclusive || false}
      value={value}
      onChange={onChange}
      sx={{
        '& .MuiToggleButton-root': {
          height: '36px',
          borderColor: 'gray',
          '&:hover': {
            backgroundColor: theme => lighten(theme.palette.background.default, 0.15),
          },
          '&.Mui-selected': {
            backgroundColor: theme => darken(theme.palette.text.primary, 0.2),
            color: theme => theme.palette.background.default,
            '&:hover': {
              backgroundColor: theme => darken(theme.palette.text.primary, 0.15),
            },
          },
        },
      }}
    >
      {options.map(option => (
        <ToggleButton key={option} value={option} sx={{ whiteSpace: 'nowrap' }}>
          {textMap[option] || option}
        </ToggleButton>
      ))}
    </ToggleButtonGroup>
  );
};