import React, { useEffect, useState } from 'react';
import _ from 'lodash';

import { Unstable_Grid2 as Grid } from '@mui/material';
import SaveIcon from '@mui/icons-material/Save';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

import { isPartnerOrCase, getCardWidth, getCardHeight, getCardBorderRadius } from '../../utils/commonUtils';


import { CardImg } from './Img';
import { DeckNameEdit } from './CustomTextField';
import { CommonIconAndTextButton, RemoveButton, } from './CustomButton';
import { CardDetails } from './CardDetails';
import { DeckCount } from './Deck';
import { DeckChart } from './Chart';


const handleRemove = (prevState, currentCard) => {
  const newDeckContents = _.cloneDeep(prevState);
  const deck = newDeckContents.deck;

  if (isPartnerOrCase(currentCard)) {
    newDeckContents[currentCard.type] = null;
  } else {
    const index = deck.findIndex(deckCard => deckCard.number === currentCard.number);
    if (index !== -1) {
      deck.splice(index, 1);
    }
  }

  return newDeckContents;
};


const CardOfDeck = ({ props, card, type }) => {
  const { setDeckContents, cardSize, handleCardClick } = props


  if (card) {
    return (
      <div>
        <div
          onClick={() => handleCardClick(card)}
          style={{ cursor: 'pointer' }}
        >
          <CardImg card={card} size={cardSize * 1.4} />
        </div>
        <div style={{ display: 'flex', justifyContent: 'flex-end', margin: 2 }}>
          <RemoveButton
            onClick={() => {
              setDeckContents(prevState => handleRemove(prevState, card));
            }}
            width={60}
            height={24}
          />
        </div>
      </div>
    )
  }

  return (
    <div>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          border: '1px solid gray',
          borderRadius: getCardBorderRadius(cardSize),
          width: getCardWidth(cardSize, type),
          height: getCardHeight(cardSize, type),
          fontWeight: 'bold',
          textTransform: 'uppercase',
        }}
      >
        {type
          ? <div>{type}</div>
          : <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <div>character</div>
            <div>or</div>
            <div>event</div>
          </div>
        }
      </div>
      <div style={{ height: 28 }} />
    </div>
  )
};


const CardsOfDeckContents = ({ props }) => {
  const { deckContents } = props
  const length = deckContents.deck.length

  //カードをクリックすると拡大
  const [selectedCard, setSelectedCard] = useState(null);
  const [open, setOpen] = useState(false);
  const handleCardClick = (card) => {
    setSelectedCard(card);
    setOpen(true);
  };

  return (
    <>
      <Grid container spacing={1} sx={{ padding: 1 }}>
        <Grid>
          {deckContents.partner
            ? <CardOfDeck props={{ ...props, handleCardClick }} card={deckContents.partner} />
            : <CardOfDeck props={props} type={'partner'} />
          }
        </Grid>
        <Grid>
          {deckContents.case
            ? <CardOfDeck props={{ ...props, handleCardClick }} card={deckContents.case} />
            : <CardOfDeck props={props} type={'case'} />
          }
        </Grid>
      </Grid>
      <Grid container spacing={1} sx={{ padding: 1, paddingRight: 0 }}>
        {deckContents.deck.map((card, index) => (
          <Grid key={card.number + index}>
            <CardOfDeck key={card.number + index} props={{ ...props, handleCardClick }} card={card} />
          </Grid>
        ))}
        {length < 40 && Array(40 - length).fill().map((_, index) => (
          <Grid key={index}>
            <CardOfDeck key={index} props={{ ...props, handleCardClick }} />
          </Grid>
        ))}
        <Grid sx={{ width: '100%', display: 'flex', justifyContent: 'flex-end' }}>
          <DeckCount length={length} />
        </Grid>
      </Grid>

      {selectedCard &&//関数で渡すので必要
        <CardDetails
          open={open}
          cardList={isPartnerOrCase(selectedCard)
            ? [selectedCard]
            : deckContents.deck.filter((card, index, self) =>
              index === self.findIndex((t) => (
                t.number === card.number
              ))//重複分を除く
            )
          }
          selectedCard={selectedCard}
          setSelectedCard={setSelectedCard}
          handleClose={() => setOpen(false)}
        />
      }

    </>
  );
}


export const DeckEditing = ({ props }) => {
  const { deckList, selectedDeckIndex, deckContents, setDeckList, setIsDeckEditing, setDeckContents } = props

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);


  return (<>
    <Grid container spacing={1} sx={{ padding: 1 }}>
      <Grid>
        <CommonIconAndTextButton
          hundleClick={() => {
            const newDeckList = [...deckList];
            newDeckList.splice(selectedDeckIndex, 1); // 元のindexの要素を削除
            newDeckList.unshift(_.cloneDeep(deckContents)); // 先頭に追加
            setDeckList(newDeckList);
            setIsDeckEditing(false)
          }}
          startIcon={<SaveIcon />}
          bgColor={'#00ffff'}
        >
          保存して戻る
        </CommonIconAndTextButton>
      </Grid>
      <Grid>
        <CommonIconAndTextButton
          hundleClick={() => {
            const newDeckList = [...deckList];
            const selectedDeck = newDeckList.splice(selectedDeckIndex, 1)[0]; // 元のindexの要素を削除
            newDeckList.unshift(selectedDeck); // 先頭に追加
            setDeckList(newDeckList);
            setIsDeckEditing(false)
          }}
          startIcon={<ArrowBackIcon />}
          bgColor={'#ffff00'}
        >
          保存しないで戻る
        </CommonIconAndTextButton>
      </Grid>

      <Grid xs={12} >
        <div style={{ width: 400 }}>
          <DeckNameEdit
            value={deckContents.deckName}
            onChange={(e) => {
              const newDeckContents = _.cloneDeep(deckContents);
              newDeckContents.deckName = e.target.value
              setDeckContents(newDeckContents);
            }}
          />
        </div>
      </Grid>

      <Grid>
        <DeckChart  deck={deckContents.deck}/>

      </Grid>


    </Grid>

    <CardsOfDeckContents props={props} />
  </>)

}
