export const DeckCount = ({ length }) => {
  if (length <= 40) return <></>;

  return <>
    <span style={{ color: 'red' }}>{length}</span>{'/40枚'}
  </>;
};


// カードを表示するコンポーネント
const CardOfDeck = ({ card, size, type }) => {
  if (card) {
    return <CardImg card={card} size={size} />;
  }

  return (
    <div
      style={{
        border: '1px solid gray',
        borderRadius: getCardBorderRadius(size / 1.4),
        width: getCardWidth(size / 1.4, type),
        height: getCardHeight(size / 1.4, type),
      }}
    />
  )
};

const DeckContentsOutline = ({ deckContents, viewWidth }) => {
  const cardSize = Math.min((viewWidth - 10 * 2) / 10 * 1.4);

  return (
    <div style={{ width: viewWidth - 20 }} >

      <div style={{ width: '100%', display: 'flex', color: '#ffffff' }}>
        <div style={{ display: 'flex' }}>
          <CardOfDeck
            card={deckContents.partner}
            size={cardSize}
          />
          <CardOfDeck
            card={deckContents.case}
            size={cardSize * 1.4}
            type={'case'}
          />
        </div>
        <div style={{ margin: 4 }}>
          <EditIcon />
        </div>

        <div style={{
          padding: '4px',
          textOverflow: 'ellipsis',
          overflow: 'hidden',
          whiteSpace: 'nowrap',
        }}>
          {deckContents.deckName}
        </div>
      </div>
      <div style={{ display: 'flex', flexWrap: 'wrap' }}>
        {deckContents.deck.map((card, index) => (
          <CardOfDeck key={card.number + index}
            card={card}
            size={cardSize}
          />
        ))}
        {deckContents.deck.length < 40 && Array(40 - deckContents.deck.length).fill().map((_, index) => (
          <CardOfDeck key={index}
            size={cardSize}
          />
        ))}
        <div style={{ marginLeft: 'auto' }}>
          <DeckCount length={deckContents.deck.length} />
        </div>
      </div>
    </div>
  );
}

export const DeckActions = ({ deckList, index, handleAddDeck, setDeckList, initialDeckContents }) => {
  return (
    <div style={{ display: 'flex', justifyContent: 'flex-end', gap: 4, marginBottom: 4 }}>
      <div style={{ width: 'auto' }}>
        <CommonButton
          onClick={() => {
            const copiedDeck = _.cloneDeep(deckList[index]);
            copiedDeck.deckName = `${copiedDeck.deckName}(コピー)`;
            handleAddDeck(copiedDeck);
          }}
        >
          <FileCopyIcon />コピーして新規追加
        </CommonButton>
      </div>

      <DeleteButton
        onClick={() => {
          if (window.confirm('削除しますか？')) {
            const newDeckList = [...deckList];
            newDeckList.splice(index, 1);

            // デッキリストが空になる場合は新しいデッキを追加
            if (newDeckList.length === 0) {
              const newDeck = _.cloneDeep(initialDeckContents);
              newDeckList.push(newDeck);
            }

            setDeckList(newDeckList);
          }
        }}
        width={36}
        height={36}
      />
    </div>
  );
};









