import React, { useEffect, useState, useMemo } from 'react';
import { useTheme } from '@mui/material/styles';


import { BottomNavigation, BottomNavigationAction, Unstable_Grid2 as Grid, IconButton, Typography } from '@mui/material';
import { darken } from '@mui/material/styles';



import { cardList, productData } from '../../constants/cards';

import { useWindowWidth } from '../../hooks/commonHooks';

import {  SearchBox } from './CustomTextField';
import { HideOnScrollAppBar } from './CustomAppBar';
import { CommonToggleButtonGroup } from './CustomToggleButton';
import { CommonSelectMenu } from './CustomSelect';
import { CardOfList } from './CardOfList';
import { Deck } from './Deck';

const getCardSize = (theme , windowWidth) =>{


  const spacing = parseInt(theme.spacing(1), 10); // Gridのspacing
  const padding = parseInt(theme.spacing(1), 10); // padding
  const minWidth = 200; // カードの最小幅

  // カードが配置される実際のスペースを計算
  const actualSpace = Math.floor(windowWidth) - padding;

  // 1行に表示できるカードの最大数を計算
  const maxCardsPerRow = Math.floor(actualSpace / minWidth);

  const totalSpacing = spacing * maxCardsPerRow

  // カード1枚あたりのスペースを計算
  return Math.floor((actualSpace - totalSpacing) / (maxCardsPerRow));

}




const Sorts = ({ sortField, sortOrder, handleSortField, toggleSortOrder }) => {
  return (
    <Grid container spacing={0.5}>
      <Grid>
        <IconButton
          onClick={toggleSortOrder}
          sx={{
            width: '36px',
            height: '36px',
            color: '#000000',
            bgcolor: darken('#ffffff', 0.2),
            boxShadow: 1,
            '&:hover': {
              bgcolor: darken('#ffffff', 0.15),
              boxShadow: 3,
            }
          }}
        >
          {sortOrder === 'asc' ? '▲' : '▼'}
        </IconButton>
      </Grid>
      <Grid>
        <CommonToggleButtonGroup
          exclusive={true}
          value={sortField}
          onChange={handleSortField}
          options={['number', 'id', 'actionPoint', 'logicPoint']}
        />
      </Grid>
    </Grid>
  );
};

const Filters = ({ filterEls }) => {
  return (
    <Grid container spacing={0.5}>
      {filterEls.map((filterEl, index) => (
        <Grid key={index} sx={{ display: 'flex', alignItems: 'center' }}>
          <div style={{ fontSize: 14, whiteSpace: 'nowrap' }}>
            {filterEl.label + '：'}
          </div>
          <CommonToggleButtonGroup
            value={filterEl.value}
            onChange={filterEl.onChange}
            options={filterEl.options}
          />
        </Grid>
      ))}
    </Grid>
  );
};

export const SortAndFilter = ({ sortField, setSortField, sortOrder, setSortOrder, filterText, setFilterText, filterProduct, setFilterProduct, filterColor, setFilterColor, filterType, setFilterType, filterRarity, setFilterRarity }) => {
  return (
    <Grid container sx={{ margin: 1, gap: 0.5 }}>

      <Grid
        sx={{
          bgcolor: '#000',
          paddingX: 1,
          paddingY: 0.5,
        }}
      >
        <Typography sx={{ fontSize: '14px', lineHeight: 1, fontWeight: 'bold' }}>ソート</Typography>
        <Sorts
          sortField={sortField}
          sortOrder={sortOrder}
          handleSortField={(event, newSortField) => {
            if (newSortField !== null) {
              setSortField(newSortField);
            }
          }}
          toggleSortOrder={() => {
            setSortOrder(prevSortOrder => prevSortOrder === 'asc' ? 'desc' : 'asc');
          }}
        />
      </Grid>

      <Grid
        sx={{
          bgcolor: '#000',
          paddingX: 1,
          paddingY: 0.5,
        }}
      >
        <Typography sx={{ fontSize: '14px', lineHeight: 1.2, fontWeight: 'bold' }}>フィルター</Typography>

        <Grid container spacing={0.5}>
          <Grid sx={{ width: 400 }}>
            <SearchBox
              value={filterText}
              onChange={(e) => {
                setFilterText(e.target.value);
              }}
            />
          </Grid>

          <Grid sx={{ width: 400 }}>
            <CommonSelectMenu
              label="商品"
              value={filterProduct}
              onChange={(event) => {
                setFilterProduct(event?.target.value || '');
              }}
              options={Object.keys(productData)}
            />
          </Grid>

          <Grid>
            <Filters
              filterEls={[
                {
                  label: '色',
                  value: filterColor,
                  onChange: (e, newFilterColor) => {
                    setFilterColor(newFilterColor);
                  },
                  options: ['青', '緑', '白', '赤', '黄', '黒'],
                },
                {
                  label: '種類',
                  value: filterType,
                  onChange: (e, newFilterType) => {
                    setFilterType(newFilterType);
                  },
                  options: ['partner', 'character', 'event', 'case'],
                },
                {
                  label: 'レア',
                  value: filterRarity,
                  onChange: (event, newFilterRarity) => {
                    setFilterRarity(newFilterRarity);
                  },
                  options: ['C', 'CP', 'R', 'RP', 'SR', 'SRP', 'D', 'PR'],
                },
              ]}
            />
          </Grid>

        </Grid>
      </Grid>
    </Grid>
  );
};



export const CardTop = () => {



  const [sortField, setSortField] = useState('number');
  const [sortOrder, setSortOrder] = useState('asc');
  const [filterText, setFilterText] = useState('');
  const [filterProduct, setFilterProduct] = useState('');
  const [filterColor, setFilterColor] = useState([]);
  const [filterType, setFilterType] = useState([]);
  const [filterRarity, setFilterRarity] = useState([]);

  const displayedCardList = useMemo(() => {

    const filteredCardList = cardList.filter(card => {
      if (!card.name) return false;

      if (filterText
        && card.number !== filterText
        && card.id !== filterText
        && !card.name.includes(filterText)
        && !card.abilities.some(ability => ability.includes(filterText))
        && !card.cutIn.includes(filterText)
        && !card.inspiration.includes(filterText)
      ) {
        return false;
      }

      if (filterProduct && card.product !== filterProduct) return false;
      if (filterColor.length > 0 && !filterColor.some(color => card.colors.includes(color))) return false;
      if (filterType.length > 0 && !filterType.includes(card.type)) return false;
      if (filterRarity.length > 0 && !filterRarity.includes(card.rarity)) return false;
      return true;
    });

    const sortedCardList = [...filteredCardList].sort((a, b) => {
      if (a[sortField] < b[sortField]) return sortOrder === 'asc' ? -1 : 1;
      if (a[sortField] > b[sortField]) return sortOrder === 'asc' ? 1 : -1;
      return 0;
    });

    return sortedCardList;
  }, [sortField, sortOrder, filterText, filterColor, filterType, filterRarity, filterProduct]);


  const [isDeckEditing, setIsDeckEditing] = useState(false)

  const [deckList, setDeckList] = useState(() => {
    const savedDeckList = localStorage.getItem('conan-cardgame-deckList');
    return savedDeckList
      ? JSON.parse(savedDeckList)
      : [{ deckName: 'デッキ名', partner: null, case: null, deck: [] }];
  });

  // selectedDeckIndexの初期値をローカルストレージから読み込む
  const [selectedDeckIndex, setSelectedDeckIndex] = useState(() => {
    const savedIndex = localStorage.getItem('conan-cardgame-selectedDeckIndex');
    return savedIndex ? Number(savedIndex) : 0;
  });

  // 選択されたデッキの情報を管理するstate
  const [deckContents, setDeckContents] = useState(() => {
    const savedDeckContents = localStorage.getItem('conan-cardgame-deckContents');
    return savedDeckContents ? JSON.parse(savedDeckContents) : deckList[selectedDeckIndex];
  });

  useEffect(() => {
    localStorage.setItem('conan-cardgame-deckList', JSON.stringify(deckList));
  }, [deckList]);

  useEffect(() => {
    localStorage.setItem('conan-cardgame-deckContents', JSON.stringify(deckContents));
  }, [deckContents]);

  useEffect(() => {
    localStorage.setItem('conan-cardgame-selectedDeckIndex', selectedDeckIndex);
  }, [selectedDeckIndex]);






  const [value, setValue] = useState(0);


  const theme = useTheme();
  const windowWidth = useWindowWidth()
  const cardSize =  getCardSize(theme,windowWidth) 







  return (
    <div>
      {value === 0 && <>
        <HideOnScrollAppBar>
          <SortAndFilter
            sortField={sortField}
            setSortField={setSortField}
            sortOrder={sortOrder}
            setSortOrder={setSortOrder}
            filterText={filterText}
            setFilterText={setFilterText}
            filterProduct={filterProduct}
            setFilterProduct={setFilterProduct}
            filterColor={filterColor}
            setFilterColor={setFilterColor}
            filterType={filterType}
            setFilterType={setFilterType}
            filterRarity={filterRarity}
            setFilterRarity={setFilterRarity}
          />
        </HideOnScrollAppBar>
        <CardOfList
          props={{
            cardSize,
          }}
          displayedCardList={displayedCardList}
          deckList={deckList}
          setDeckList={setDeckList}
          deckContents={deckContents}
          setDeckContents={setDeckContents}
          viewWidth={windowWidth}
          isDeckEditing={isDeckEditing}
        />
      </>}
      {value === 1 &&
        <Deck
          props={{
            cardSize
          }}
          deckList={deckList}
          setDeckList={setDeckList}
          selectedDeckIndex={selectedDeckIndex}
          setSelectedDeckIndex={setSelectedDeckIndex}
          deckContents={deckContents}
          setDeckContents={setDeckContents}
          viewWidth={Math.min(windowWidth, 960)}
          isDeckEditing={isDeckEditing}
          setIsDeckEditing={setIsDeckEditing}
        />
      }
      <div style={{ width: '100%', position: 'fixed', bottom: 20, zIndex: 10 }}>
        <BottomNavigation
          value={value}
          onChange={(event, newValue) => {
            setValue(newValue);
          }}
          showLabels
          sx={{ backgroundColor: 'rgba(0, 0, 0, 0.7)' }} // ここで背景色を半透明に設定
        >
          <BottomNavigationAction
            label="カード"
            sx={{
              '& .MuiBottomNavigationAction-label': {
                fontSize: '1.1rem', // 非選択時のフォントサイズ
                fontWeight: 'bold', // 非選択時のフォントウェイト
                '&.Mui-selected': {
                  fontSize: '1.2rem', // 選択時のフォントサイズ
                },
              },
            }}
          />
          <BottomNavigationAction
            label={isDeckEditing ? 'デッキ(編集中)' : 'デッキ'}
            sx={{
              '& .MuiBottomNavigationAction-label': {
                fontSize: '1.1rem', // 非選択時のフォントサイズ
                fontWeight: 'bold', // 非選択時のフォントウェイト
                '&.Mui-selected': {
                  fontSize: '1.2rem', // 選択時のフォントサイズ
                },
              },
            }}
          />
        </BottomNavigation>
      </div>

    </div>
  );
};


