import React from 'react';
import { AppBar, useScrollTrigger, Slide } from '@mui/material';

export const HideOnScrollAppBar = ({ backgroundColor, children, ...props }) => {
  const trigger = useScrollTrigger();

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      <AppBar position="sticky" {...props}      >
        {children}
      </AppBar>
    </Slide>
  );
};




