import React, { useState, useEffect } from 'react';
import {  Modal, } from '@mui/material';

import { CardImg } from './Img';
import { CloseIconButton, NextIconButton, PrevIconButton } from './CustomButton';

export const CardDetails = ({ cardList, open, selectedCard, setSelectedCard, handleClose }) => {
  const [dimensions, setDimensions] = useState({ width: window.innerWidth, height: window.innerHeight });

  const size = 1000

  // console.log(selectedCard,cardList)

  useEffect(() => {
    const handleResize = () => {
      setDimensions({ width: window.innerWidth, height: window.innerHeight });
    };
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const handleNext = () => {
    const currentIndex = cardList.findIndex(card => card.number === selectedCard.number);
    const nextIndex = (currentIndex + 1) % cardList.length;
    setSelectedCard(cardList[nextIndex]);
  };

  const handlePrev = () => {
    const currentIndex = cardList.findIndex(card => card.number === selectedCard.number);
    const prevIndex = (currentIndex - 1 + cardList.length) % cardList.length;
    setSelectedCard(cardList[prevIndex]);
  };

  const modalWidth = Math.min(dimensions.width, 1250);
  const modalHeight = Math.min(dimensions.height, 1250);

  const maxWidth = selectedCard?.type === 'case' ? size : size / 1.4;
  const maxHeight = (selectedCard?.type === 'case' ? size / 1.4 : size);

  const w = Math.min(dimensions.width / maxWidth, 1);
  const h = Math.min(dimensions.height / maxHeight, 1);

  const wh = Math.min(w, h);

  let viewSize = size * wh * 0.96;
  if ((viewSize + 100) > dimensions.height) {
    viewSize = dimensions.height - 100
  }

  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
    >
      <div
        style={{
          width: modalWidth,
          height: modalHeight,
          display: 'flex', alignItems: 'center', justifyContent: 'center',
          position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)',
        }}
        onClick={handleClose}  // Add this line
      >
        <div
          style={{
            width: selectedCard.type === 'case' ? viewSize : viewSize / 1.4,
            height: selectedCard.type === 'case' ? viewSize / 1.4 : viewSize,
            display: 'flex', alignItems: 'center', justifyContent: 'center',
            position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)'
          }}
          onClick={(event) => event.stopPropagation()}  // Add this line
        >
          {selectedCard &&
            <CardImg card={selectedCard} size={viewSize} boxShadow={'0px 0px 5px 5px rgba(0,0,0,0.75)'}
            />
          }
          {cardList.length >= 2 && <>
            <PrevIconButton sx={{ position: 'absolute', left: 0, bottom: -45 }} width={40} onClick={(event) => { event.stopPropagation(); handlePrev(); }} />
            <NextIconButton sx={{ position: 'absolute', right: 0, bottom: -45 }} width={40} onClick={(event) => { event.stopPropagation(); handleNext(); }} />
          </>}
          <CloseIconButton sx={{ position: 'absolute', right: 0, top: -45 }} width={40} onClick={(event) => { event.stopPropagation(); handleClose(); }} />
        </div>
      </div>
    </Modal>
  );

};

