import React from 'react';
import { Button, IconButton } from '@mui/material';

import { darken } from '@mui/system';

import DeleteIcon from '@mui/icons-material/Delete';
import PrevIcon from '@mui/icons-material/NavigateBefore';
import NextIcon from '@mui/icons-material/NavigateNext';
import CloseIcon from '@mui/icons-material/Close';
import ViewListIcon from '@mui/icons-material/ViewList';
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import RemoveIcon from '@mui/icons-material/Remove';


export const CommonButton = ({ onClick, width = '100%', height, bgColor = '#ffffff', children }) => {
  return (
    <Button
      aria-label="delete"
      color="primary"
      onClick={onClick}
      sx={{
        width: width, // 幅を設定します
        height: height ?? 36, // 高さを設定します
        color: '#ffffff',
        bgcolor: darken(bgColor, 0.5),
        boxShadow: 1,
        '&:hover': {
          bgcolor: darken(bgColor, 0.4),
          boxShadow: 3,
        }
      }}
    >
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', width: '100%', lineHeight: 1.2 }}>
        {children}
      </div>
    </Button>
  )
}

export const CommonIconAndTextButton = ({ startIcon, hundleClick, bgColor,height, children }) => {

  return (
    <Button
      variant='contained'
      startIcon={startIcon}
      onClick={hundleClick}
      sx={{
        // width: width, // 幅を設定します
        height: height ?? 36, // 高さを設定します
        color: '#ffffff',
        bgcolor: darken(bgColor, 0.5),
        boxShadow: 1,
        '&:hover': {
          bgcolor: darken(bgColor, 0.4),
          boxShadow: 3,
        }
      }}

    >
      {children}
    </Button>


  )

}

export const CloseIconButton = ({ onClick, sx, width }) => {
  return (
    <IconButton
      sx={{
        ...sx,
        width,
        height: width,
        backgroundColor: darken('#ffff00', 0.5),
        '&:hover': {
          backgroundColor: darken('#ffff00', 0.3),
        },
        boxShadow: '0px 0px 5px 5px rgba(0,0,0,0.75)', // ここで影を追加

      }}
      onClick={onClick}
    >
      <CloseIcon />
    </IconButton>
  );
};

