export const DeleteButton = ({ onClick, width, height }) => {
  const bgColor = '#ff7f7f'
  return (
    <Button
      aria-label="delete"
      color="primary"
      onClick={onClick}
      sx={{
        color: '#ffffff',
        bgcolor: darken(bgColor, 0.5),
        boxShadow: 1,
        width: width ?? 40, // 幅を設定します
        minWidth: width ?? 40,
        height: height ?? 40, // 高さを設定します
        '&:hover': {
          bgcolor: darken(bgColor, 0.4),
          boxShadow: 3,
        }
      }}
    >
      <DeleteIcon />
    </Button>
  )
}

