import React from 'react';
import { darken } from '@mui/material/styles';

import { cardData } from '../../constants/cards';
import { Img } from './Img';


const width = 20;
const height = 60;

const typeColors = {
  character: darken('#7f7fff', 0),
  event: darken('#ff7f7f', 0.2)

}

export const DeckChart = ({ deck }) => {
  const levelCounts = {
    1: { character: 0, event: 0 },
    2: { character: 0, event: 0 },
    3: { character: 0, event: 0 },
    4: { character: 0, event: 0 },
    5: { character: 0, event: 0 },
    6: { character: 0, event: 0 },
    7: { character: 0, event: 0 },
    8: { character: 0, event: 0 }
  };
  const typeCounts = {
    character: 0,
    event: 0,
  }
  const keyWordCounts = {
    迅速: 0,
    突撃: 0,
    '突撃［事件］': 0,
    '突撃［キャラ］': 0,
    ブレット: 0,
    ミスリード1: 0,
    捜査1: 0,
  }
  let cutInCounts = 0
  let inspirationCounts = 0




  for (let card of deck) {
    let id = card.id;
    let level = cardData[id].level;
    let type = cardData[id].type;
    let abilities = cardData[id].abilities;

    levelCounts[level][type]++;
    typeCounts[type]++;

    // カードの各能力に対してループを行います。
    for (let ability of abilities) {
      // keyWordCountsの各キーワードに対してループを行います。
      for (let key in keyWordCounts) {
        // 能力がキーワードを含む場合、その数をカウントします。
        if (ability.includes('〚' + key + '〛')) {
          keyWordCounts[key]++;
        }
      }
    }

    if (cardData[id].cutIn) {
      cutInCounts++
    }
    if (cardData[id].inspiration) {
      inspirationCounts++
    }


  }



  const maxCount = Math.max(...Object.values(levelCounts).map(counts => counts.character + counts.event));

  return (
    <div style={{ lineHeight: 1,             display: 'flex',
    flexDirection: 'column',
    gap: 4,
}}>
      <div>
        <div style={{ display: 'flex', justifyContent: 'center', gap: 4, fontWeight: 'bold' }}>
          {['character', 'event'].map(type => (
            <div
              key={type}
              style={{
                backgroundColor: typeColors[type],
                borderRadius: 4,
                height: 20,
                paddingLeft: 4,
                paddingRight: 4,
                display: 'flex',
                alignItems: 'center',
                flexGrow: 1,
              }}
            >
              <span style={{ fontSize: 14 }}> {type}:</span>
              <span style={{ fontSize: 16 }}>{typeCounts[type]}</span>
            </div>
          ))}
        </div>
        <div style={{ display: 'flex', gap: 1 }}>
          {Object.entries(levelCounts).map(([level, counts]) => {
            return (
              <div key={level}
                style={{
                  width,
                  display: 'flex',
                  flexDirection: 'column',
                  fontWeight: 'bold',
                }}
              >
                <div style={{ lineHeight: 1, textAlign: 'center' }}>{counts.character + counts.event}</div>
                <div
                  style={{
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'flex-end',
                    height,
                    position: 'relative',
                  }}>
                  <div
                    style={{
                      position: 'absolute',
                      bottom: 0,
                      width,
                      height: '100%',
                      backgroundColor: 'dimgray',
                    }}
                  />
                  <div
                    style={{
                      position: 'absolute',
                      bottom: 0,
                      width,
                      height: `${Math.floor(counts.character / maxCount * 100)}%`,
                      backgroundColor: typeColors.character,
                    }}
                  />
                  <div
                    style={{
                      position: 'absolute',
                      bottom: `${Math.floor(counts.character / maxCount * 100)}%`,
                      width,
                      height: `${Math.floor(counts.event / maxCount * 100)}%`,
                      backgroundColor: typeColors.event,
                    }}
                  />
                </div>
                <div style={{
                  backgroundColor: 'white',
                  borderRadius: '50%',
                  color: 'black',
                  textAlign: 'center',
                  width,
                  height: 20,
                  lineHeight: '20px',
                }}>{level}</div>
              </div>
            )
          })}
        </div>
      </div>

      <div style={{
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '1px',
        
      }}>
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            gap: '1px',
            fontWeight: 'bold',
            lineHeight: 1.2,
          }}>
          {Object.entries(keyWordCounts).map(([keyword, count]) => (
            count > 0 && // countが0より大きい場合のみ表示
            <div key={keyword}
              style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center', // 高さ方向で中央寄せ
                backgroundColor: 'dimgray',
                borderRadius: 4,
                paddingLeft: 4,
                paddingRight: 4,
              }}>
              <span
                style={{
                  fontSize: 14,
                }}>
                {keyword}
              </span>
              <span
                style={{ fontSize: 16 }}>{count}</span>
            </div>
          ))}
        </div>

        <div style={{ display: 'flex', gap: 1, fontWeight: 'bold' }}>
          <div
            style={{
              display: 'flex', // 追加
              alignItems: 'center', // 追加
              backgroundColor: 'dimgray',
              paddingLeft: 4,
              paddingRight: 4,
              borderRadius: 4,
              gap:2,
              flexGrow: 1,
              justifyContent: 'space-between',
            }}
          >
            <Img name={'icon-cutIn'} size={20} />
            {cutInCounts}
          </div>
          <div
            style={{
              display: 'flex', // 追加
              alignItems: 'center', // 追加
              backgroundColor: 'dimgray',
              paddingLeft: 4,
              paddingRight: 4,
              borderRadius: 4,
              gap:2,
              flexGrow: 1,
              justifyContent: 'space-between',

            }}
          >
            <Img name={'icon-inspiration'} size={20} />
            {inspirationCounts}
          </div>
        </div>
      </div>



    </div>
  );
};
