import React, {useCallback, useState, useEffect } from 'react';
import _ from 'lodash';

import { Unstable_Grid2 as Grid, TablePagination } from '@mui/material';

import { CardImg } from './Img';
import { CardDetails } from './CardDetails';
import { QuantityChange } from './CustomRating';
import { isPartnerOrCase } from '../../utils/commonUtils';

const rowsPerPageOptions = [20, 40, 100, 200, 400]


const getDifferentNumbersWithSameId = (deck, currentCard, sameIdCount) => {

  //操作したIDと同IDがなければ空配列
  if (!sameIdCount) return [];

  //操作したIDと同IDで異番号のカードの全ての番号s
  const differentNumbersWithSameId = deck.filter(card => card.id === currentCard.id && card.number !== currentCard.number).map(card => card.number);

  //若い番号から削除対象にするためソート
  differentNumbersWithSameId.sort();

  return differentNumbersWithSameId;
};

export const CardOfList = ({ props, displayedCardList, deckContents, setDeckContents, viewWidth, isDeckEditing }) => {
  const { cardSize } = props

  const [selectedCard, setSelectedCard] = useState(null);
  const [open, setOpen] = useState(false);




  const handleQuantityChange = (currentCard, newValue) => {
    setDeckContents(prevState => {
      const newDeckContents = _.cloneDeep(prevState);

      if (isPartnerOrCase(currentCard)) {
        newDeckContents[currentCard.type] = newValue
          ? { number: currentCard.number, id: currentCard.id, type: currentCard.type }
          : null;
        return newDeckContents;
      }

      let deck = newDeckContents.deck;

      //デッキに既にある同IDカードの枚数
      const sameIdCount = deck.filter(card => card.id === currentCard.id).length;
      //デッキに既にある異IDの同番号カードの番号s(若い番号から整列済)
      let differentNumbersWithSameId = getDifferentNumbersWithSameId(deck, currentCard, sameIdCount);

      //一旦、操作したカードを除く
      deck = deck.filter(card => !(card.id === currentCard.id && card.number === currentCard.number));
      //操作したカードを追加する
      for (let i = 0; i < newValue; i++) {
        deck.push({ number: currentCard.number, id: currentCard.id, type: currentCard.type });
      }

      //操作したカード追加後の、同IDカードs
      let sameIdCards = deck.filter(card => card.id === currentCard.id);

      //同IDカード枚数上限は3なので、追加して超えた分を削除する
      if (sameIdCards.length > 3) {
        while (sameIdCards.length > 3) {

          //デッキから、differentNumbersWithSameId[0]のカード番号を持つ要素を削除
          const index = deck.findIndex(card => card.number === differentNumbersWithSameId[0]);
          if (index !== -1) {
            deck.splice(index, 1);
          }

          //関係する配列を更新し繰り返す
          differentNumbersWithSameId.shift();
          sameIdCards = deck.filter(card => card.id === currentCard.id);
        }
      }

      deck.sort((a, b) => a.number.localeCompare(b.number));
      newDeckContents.deck = deck;

      return newDeckContents;
    });
  };




  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    window.scrollTo(0, 0);
  };
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  // const getTruePage = () => {//page更新よりTablePaginationの方が早いため、架空のpageが表示されるのを防ぐ
  //   return Math.min(page, Math.floor(displayedCardList.length / rowsPerPage))
  // }
  const getTruePage = useCallback(() => {//getTruePage 関数はコンポーネントのレンダリングごとに新しく作成されるため、useEffectフックが不必要に頻繁に実行される可能性があります。これを避けるためには、getTruePage 関数を useCallback フックを使用してメモ化することができます：
    return Math.min(page, Math.floor(displayedCardList.length / rowsPerPage));
  }, [page, displayedCardList, rowsPerPage]);
  const truePage = getTruePage()
  const paginatedCardList = displayedCardList.slice(truePage * rowsPerPage, (truePage + 1) * rowsPerPage);

  useEffect(() => {//この警告を解消するためには、getTruePage 関数を依存配列に追加することが一般的な解決策です：
    setPage(getTruePage());
  }, [displayedCardList, getTruePage]);

  // useEffect(() => {
  //   console.log(page)
  // }, [page]);

  return (
    <>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <TablePagination
          component="div"
          count={displayedCardList.length}
          page={getTruePage()}
          onPageChange={handleChangePage}
          rowsPerPage={rowsPerPage}
          onRowsPerPageChange={handleChangeRowsPerPage}
          rowsPerPageOptions={rowsPerPageOptions}
        />
      </div>

      <Grid container spacing={1} sx={{ padding: 1, paddingRight: 0, width: viewWidth }} >
        {paginatedCardList.map(card => {
          return (
            <Grid key={card.number} >
              <div
                onClick={() => {
                  setSelectedCard(card);
                  setOpen(true);
                }}
                style={{ cursor: 'pointer' }}
              >
                <CardImg card={card} size={cardSize * 1.4} />
              </div>
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  fontSize: 14,
                }}>
                {card.number}
                {isDeckEditing &&
                  <QuantityChange
                    name={`card-rating-${card.number}`}
                    value={isPartnerOrCase(card)
                      ? (deckContents[card.type]?.number === card.number ? 1 : 0)
                      : deckContents.deck.filter(deckCard => deckCard.number === card.number).length
                    }
                    max={isPartnerOrCase(card)
                      ? 1
                      : 3
                    }
                    onChange={(event, newValue) => handleQuantityChange(card, newValue)}
                  />
                }
              </div>
            </Grid>
          )
        })}


      </Grid>

      {displayedCardList.length > 1 &&
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <TablePagination
            component="div"
            count={displayedCardList.length}
            page={getTruePage()}
            onPageChange={handleChangePage}
            rowsPerPage={rowsPerPage}
            onRowsPerPageChange={handleChangeRowsPerPage}
            rowsPerPageOptions={rowsPerPageOptions}
          />
        </div>
      }

      {selectedCard &&
        <CardDetails
          open={open}
          cardList={displayedCardList}
          selectedCard={selectedCard}
          setSelectedCard={setSelectedCard}
          handleClose={() => setOpen(false)}
        />
      }
    </>
  );
};