

export const isPartnerOrCase = (card) => {
  return card.type === 'partner' || card.type === 'case';
}


export const getCardWidth = (size, type) => {
  return  type === 'case' ? size * 1.4 : size;
}
export const getCardHeight = (size, type) => {
  return  type === 'case' ? size : size * 1.4;
}
export const getCardBorderRadius = (size) =>{
  return size * 1.4 / 25;
}
